<template>
  <div class="ao-asset-return" @click="back">
    <div style="height:1px;"></div>
    <div class="header">
      <div class="return"><i class="el-icon-arrow-left"></i>{{ r }}</div>
      <h2>{{ title }}</h2>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AOAssetReturn',
  data() {
    return {
      title: '',
      r: this.$t('text.btn')
    }
  },
  mounted() {
    var __data = this.$store.state.__data
    this.title = __data && __data.title ? __data.title : ''
  },
  methods: {
    back() {
      this.$router.back(-1)
    }
  }
}
</script>

<style scoped lang="scss">
.ao-asset-return {
  .header {
    padding: 15px 30px;
    margin: 10px;
    color: #c7cce6;
    cursor: pointer;
    background: #171c2b;
    text-align: center;
    .return{
      position: absolute;
    }
  }
}
</style>



