<template>
  <div class="ao-nav" :style="aoStyle">
    <a class="logo" href="#/">
      <img src="/static/image/logo.png" />
    </a>
    <ul class="nav">
      <li v-for="(item, index) in nav" :key="index" @click="path(item.path)">{{ item.title }}</li>
    </ul>
    <ul class="fr">
      <li class="lang" @click="path('/login')">
        <span class="iconfont icon-denglu"></span>
        <span v-if="!phone">{{ btn[0] }}</span>
        <span>{{ phone }}</span>
        <div v-if="token" class="lang-box">
          <div
            class="lang-item"
            v-for="(item, index) in menus"
            :key="index"
            @click.stop="path(item.path)"
          >
            <!-- <span class="iconfont icon-denglu"></span> -->
            <span>{{ item.title }}</span>
          </div>
        </div>
      </li>
      <li v-if="!token" @click="path('/register')">
        <span class="iconfont icon-fill"></span>
        <span>{{ btn[1] }}</span>
      </li>
      <li class="lang">
        <span class="iconfont icon-yuyan"></span>
        <span>{{ btn[2] }}</span>
        <span class="iconfont iconfont-d icon-xiangxiajiantoushixin"></span>
        <span class="iconfont iconfont-u icon-xiangshang"></span>
        <div class="lang-box">
          <div class="lang-item" v-for="(item, index) in lang" :key="index" @click="setLang(index)">
            <img :src="item.icon" />
            <span>{{ item.lang }}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import langzh from "@/assets/lang-zh.png";
import langen from "@/assets/lang-en.png";
import langtw from "@/assets/lang-tw.png";
import langrb from "@/assets/lang-rb.png";
import { mapGetters } from "vuex";
import { setLang, getLang } from "@/utils/auth";

export default {
  props: {
    aoStyle: {
      type: Object,
      default: () => {
        return {
          background: "rgba(0,0,0,.15)",
          position: "absolute"
        };
      }
    }
  },
  data() {
    return {
      btn: this.$t("nav.btn"),
      nav: this.$t("nav.nav"),
      nav2: this.$t("nav.nav2"),
      menus: this.$t("nav.menus"),
      lang: this.$t("nav.lang")
    };
  },
  computed: {
    ...mapGetters(["token", "phone"])
  },
  mounted() {
    var key = getLang()
    var obj = this.lang.find(o => o.key == key)
    this.btn[this.btn.length - 1] = obj.lang
    if (this.token) {
      this.nav = this.nav.concat(this.nav2);
    }
  },
  methods: {
    setLang(index) {
      var key = this.lang[index].key;
      setLang(key);
      this.$i18n.locale = key;
      location.reload()
    },
    path(path) {
      if (path) {
        this.$router.push({ path: path });
      } else {
        this.$store.dispatch("FedLogOut").then(() => {
          location.reload();
        });
      }
    }
  }
};
</script>

<style scoped lang="scss">
$nav-height: 3rem;
$nav-background-color: #1b2945;
$font-size-sm: 0.875rem;
.ao-nav {
  // position: absolute;
  top: 0;
  left: 0;
  height: $nav-height;
  line-height: $nav-height;
  width: 100%;
  min-width: 1200px;
  padding: 0 1.875rem;
  // background: rgba(0,0,0,.15);

  z-index: 99;
  .logo {
    float: left;
    img {
      height: 2rem;
      width: 9rem;
      margin-top: 0.25rem;
    }
  }
  li {
    float: left;
    height: $nav-height;
    line-height: $nav-height;
    text-align: center;
    font-size: $font-size-sm;
    padding: 0 11px;
    display: block;
    cursor: pointer;
    color: #87a2cd;
  }
  .nav {
    li:hover {
      color: #fff;
      border-bottom: 2px solid #357ce1;
    }
  }
  .fr {
    float: right;
    li {
      position: relative;
      span {
        color: #87a2cd;
        font-size: $font-size-sm;
        &.iconfont-u {
          display: none;
        }
      }
      &:hover span {
        color: #fff;
        &.iconfont-d {
          display: none;
        }
        &.iconfont-u {
          display: inline-block;
        }
      }
      &.lang:hover {
        .lang-box {
          display: block;
        }
      }
      .lang-box {
        display: none;
        width: 7.5rem;
        background: #fff;
        position: absolute;
        top: 2.8rem;
        border-radius: 6px;
        z-index: 999;
        // left: -58px;
        .lang-item {
          &:hover {
            background: #eee;
          }
          &:first-child {
            border-top-right-radius: 6px;
            border-top-left-radius: 6px;
          }
          &:last-child {
            border-bottom-left-radius: 6px;
            border-bottom-right-radius: 6px;
          }
          img {
            width: 1.5rem;
            height: 1.5rem;
            vertical-align: middle;
          }
          span {
            color: #333;
            text-align: center;
            vertical-align: middle;
          }
          cursor: pointer;
          padding: 0 10px;
          height: 3.125rem;
        }
      }
    }
  }
}
</style>



