import request from '@/utils/request'

export function getCurrency(data)
{
    return request({
        url: 'web/getCurrency',
        method: 'get',
        params: data
    })
}

export function getCurrencyType()
{
    return request({
        url: 'web/getCurrencyType',
        method: 'get'
    })
}
